.reviews {
  font-family: DM Sans;
}

.reviews__swap__wrap {
  position: relative;
}

.reviews__image__wrap {
  border-radius: 50%;
  width: 85px;
  height: 85px;
  margin: 80px auto 29px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.reviews__image__wrap__copy {
  border-radius: 50%;
  width: 85px;
  height: 85px;
  margin: 0 auto 29px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 117.5px;
  transform: translateX(100vw);
}

.reviews__image__wrap__copy__2 {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: translateX(200vw);
}

.reviews__image__wrap.reviews__swap1 {
  transform: translateX(0);
}

.reviews__image__wrap__copy.reviews__swap1 {
  transform: translateX(100vw);
}

.reviews__image__wrap__copy__2.reviews__swap1 {
  transform: translateX(200vw);
}

.reviews__image__wrap.reviews__swap2 {
  transform: translateX(-100vw);
}

.reviews__image__wrap__copy.reviews__swap2 {
  transform: translateX(-50%);
}

.reviews__image__wrap__copy__2.reviews__swap2 {
  transform: translateX(100vw);
}

.reviews__image__wrap.reviews__swap3 {
  transform: translateX(-200vw);
}

.reviews__image__wrap__copy.reviews__swap3 {
  transform: translateX(-100vw);
}

.reviews__image__wrap__copy__2.reviews__swap3 {
  transform: translateX(-50%);
}

.reviews__image {
  width: 85px;
  height: 85px;
}

.reviews__text__wrap {
  background-color: #fafafa;
  width: 87.5%;
  margin: 0 auto 30px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.reviews__text__wrap__copy {
  background-color: #fafafa;
  width: 87.5%;
  margin: 0 auto 30px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(100vw);
}

.reviews__text__wrap__copy__2 {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: translateX(200vw);
}

.reviews__text__wrap.reviews__swap1 {
  transform: translateX(0);
}

.reviews__text__wrap__copy.reviews__swap1 {
